.main {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    min-height: 700px;
}

.Hello {
    height: 25%;
    width: 100%;
    background: #ffffff;
    margin-bottom: 20px;
    box-sizing: border-box;
    border: 2px solid #397242;
    border-radius: 15px;
    box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.3);
}

.Hello .title {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 15px;
    font-weight: bold;
}

.bt1 {
    width: 65%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin:auto;
}

.rank {
    height: 35%;
    width: 100%;
    background: #ffffff;
    margin-bottom: 20px;
    box-sizing: border-box;
    border: 2px solid #397242;
    border-radius: 15px;
    box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.3);
}

.rank .title {
    font-weight: bold;
}
.popularRank {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.etc {
    height: 33%;
    display: flex;
    justify-content: space-between; 
}

.Notice, .Question {
    flex-basis: calc(50% - 10px);
    height: 100%;
    background: #ffffff;
    margin-bottom: 20px;
    box-sizing: border-box;
    border: 2px solid #397242;
    border-radius: 15px;
    box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.3);
    font-weight: bold;
}

.Notice {
    text-align: center;
}

.Question {
    text-align: center;
}

