.Community_main {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    min-height: 700px;
}

/*카테고리*/
.Community_menu {
    width: 100%;
    height: 10%;
    margin-bottom: 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    min-height: 25px;
}

.Community_groupcreate button{
    width: 100px;
    height: 30px;
    margin: 10px;
    min-height: 40px;
    background-color: #D9D9D9;
    border: 0px;
    border-radius: 50px;
    font-size: 15px;
}

/*그룹 생성*/
.Community_groupcreate {
    width: 65%;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 10px;
    margin-right: 10px;
}

.Community_groupbtn {
    width: 30%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 10px;
}

.Community_groupbtn button {
    width: 150px;
    height: 55px;
    font-size: 20px;
    background-color: #8CBA51;
    text-decoration: none;
    /* 밑줄 제거 */
    color: #ffffff;
    border-radius: 15px;
    border: 0px solid;
    font-weight: 700;
}

.Community_link {
    text-decoration: none;
    /* 밑줄 제거 */
    color: inherit;
}

/*인기랭크*/
.Community_rank {
    height: 31%;
    width: 100%;
    background: #ffffff;
    margin-bottom: 20px;
    box-sizing: border-box;
    border: 2px solid #397242;
    border-radius: 15px;
    box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.3);

}

.Community_title {
    width: 100%;
    height: 20%;
    font-size: 1.5rem;
    color: #01352C;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
}

.Community_popularRank {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;

}

/*인기순위그룹 스타일*/
.Community_Rgroup {
    display: grid;
    width: 100%;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(1rem, 1fr));
    align-items: start;
    margin: 13px 10px 10px 10px;
}

.Community_item {
    background-color: #fff;
    border-radius: 10px;
    border: 2px solid #e4e4e4;
    overflow: hidden;
    cursor: pointer;
    transition: 0.2s;
    padding: 10px;
}

.Community_item:hover {
    transform: translateY(-0.5%);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.Community_cardimg {
    display: block;
    width: 100%;
    height: 120px;
    object-fit: cover;
    border-radius: 10px;
}

.Community_Groupname {
    font-size: 17px;
    font-weight: 500;
    color: #0d0d0d;
    margin-bottom: -7px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
}

/*그룹 목록*/
.Community_GroupList {
    width: 100%;
    height: 45vh;
    box-sizing: border-box;
    padding: 10px;
    border: 2px solid #397242;
    border-radius: 15px;
    box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.3);
    margin-bottom: 5px;
    min-height: 330px;
}

/*그룹 리스트 스타일*/
.Community_Grouplists {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 93%;
    overflow: hidden;
    padding: 0px 10px 0px 10px;
    }
.Community_gallery {
    display: grid;
    width: 100%;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(1rem, 1fr));
    align-items: start;
}
.Community_gallery .Community_Gitem {
    background-color: #fff;
    border-radius: 15px;
    border: 2px solid #e4e4e4;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    transition: 0.2s;
    padding: 5px;
    
}
/* 1행의 아이템은 왼쪽에서 오른쪽으로 배치 */
.Community_gallery .Community_Gitem:nth-child(1), .Community_gallery .Community_Gitem:nth-child(3), .Community_gallery .Community_Gitem:nth-child(5), .Community_gallery .Community_Gitem:nth-child(7), .Community_gallery .Community_Gitem:nth-child(9), .Community_gallery .Community_Gitem:nth-child(11) {
    grid-row: 1;
}

/* 2행의 아이템은 오른쪽에서 왼쪽으로 배치 */
.Community_gallery .Community_Gitem:nth-child(2), .Community_gallery .Community_Gitem:nth-child(4), .Community_gallery .Community_Gitem:nth-child(6), .Community_gallery .Community_Gitem:nth-child(8), .Community_gallery .Community_Gitem:nth-child(10),.Community_gallery .Community_Gitem:nth-child(12) {
    grid-row: 2;
}
.Community_Gcardimg {
    display: block;
    width: 100%;
    height: 14vh;
    min-height: 100px;
    object-fit: cover;
    border-radius: 15px;
}
.Community_Ggroupname {
    font-size: 14px;
    font-weight: 500;
    color: #0d0d0d;
    margin-bottom: 1.5px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
}
/*캡션*/
.Community_caption {
    position: absolute;
    width: 93%;
    /*해당 크기 값은 본 코드에서 크기가 바뀔수 있다.*/
    height: 94%;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    border-radius: 15px;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    z-index: 10;
    font-size: 15px;
    padding: 10px;
}

.Community_Gitem:hover{
    transform: translateY(-0.5%);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.Community_Gitem:hover .Community_caption {
    opacity: 1;
    transform: translateY(-200px);
    -moz-transform: translateY(-140px);
    -webkit-transform: translateY(-101%);
    -o-transform: translateY(-200px);
    -ms-transform: translateY(-500px);
}

.Community_captionname {
    font-size: 20px;
}

.Community_captionname,
.Community_caption p {
    color: #fff;
    text-align: center;
}
/*이동 버튼*/
.Community_Grouplistbtn {
    width: 100%;
    height: 7%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Community_Grouplistbtn button{
    padding-left: 20px;
    background: none;
    cursor: pointer;
    border: none;
}